/****************************** DEFAULTS *******************************/

body {
  background-color: #323232;
  margin: 0;
  font-family: 'Jost', sans-serif;
}

h1 {
  font-size: 6rem;
  text-align: center;
  font-family: 'Exo', sans-serif;
  color: #14FFEC;

}

h2 {
  font-size: 3.5rem;
  text-align: center;
  font-family: 'Exo', sans-serif;
  color: #5cfff1;

}
h3 {
  font-size: 2rem;
  font-family: 'Exo', sans-serif;
  color: #b9fff9;

}

p {
  font-size: 1.25rem;
}


/****************************** CLASSES *******************************/

.app-header {
  display: flex;
  padding: 10vh 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #212121;
}

.app-header h1 {
  margin-top: 0;
}

.app-header img{
  height: 30vh;
  width: 30vh;
  border-radius: 50%;
  border: solid #14FFEC 5px ;
}

.info {
  margin: 10vh 0;
}

.my-hr {
  font-family: 'Caveat', cursive;
  font-size: 2em;
  color: #575757;
  text-align: center;
}

.about-paragraph{
  min-height: 20vh;
  margin-bottom: 0;
  padding-top: 5vh;
  padding-bottom: 5vh;
  background-color: #323232;
}

.about-paragraph h2 {
  margin-top: 0;
}

.about-paragraph p {
  margin: 0;
  text-align: center;
  color: white;
}

.skills {
  margin: 0;
  min-height: 20vh;
  color: white;
  background-color: #323232;
  line-height: 2;
}

.skills h2 {
  padding-top: 5vh;
  margin-top: 0;
}


.programming {
  width: 60%;
  margin: 5vh auto 0;
  height: 300px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.programming img{
  height: 200px;
  margin-right: 5%;
  float: left;
}

.programming div {
  text-align: center;
}

.programming h3 {
  margin: 1vh;
}

.gamedev {
  width: 60%;
  margin: 5vh auto 0;
  height: 300px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.gamedev img{
  order: 2;
  height: 200px;
  margin-left: 5%;
  float: right;
}

.gamedev div {
  text-align: center;
}

.gamedev h3 {
  margin: 1vh;
}